import React from 'react';
import { Row, Col } from 'antd';
import CardTitle from '../../../components/common/CardTitle/index';
import Card from '../../../components/common/Card/index';
import './styles.scss';
import Profile from './Profile';
import TeacherClasses from './TeacherClasses';
import useCurrentUserId from '../../../hook/useCurrentUserId';
import OwnerClasses from './OwnerClasses';
import TestOrganizations from './TestOrganizations';

export default () => {
  const currentUserId = useCurrentUserId();
  if (!currentUserId) {
    return null;
  }
  return (
    <Row type="flex">
      <Col span={10}>
        <Card>
          <CardTitle title="Thông tin" />
          <Profile />
        </Card>
      </Col>
      <Col span={14}>
        <Card>
          <CardTitle title="Lớp phụ trách" />
          <TeacherClasses />
        </Card>
      </Col>
      <Col span={10}>
        <Card>
          <CardTitle title="Lớp cố vấn" />
          <OwnerClasses />
        </Card>
      </Col>
      <Col span={14}>
        <Card>
          <CardTitle title="Lịch thi" />
          <TestOrganizations />
        </Card>
      </Col>
    </Row>
  );
};



