import React from 'react';
import { Link } from 'react-router-dom';
import Btn from '../../../components/common/Btn/Btn';
import BtnGroup from '../../../components/common/Btn/BtnGroup';
import useCurrentUserId from '../../../hook/useCurrentUserId';
import TableData from '../../../react-uikit/component/table/TableData';
import { getTeacherOwnerClass } from '../../../services/studentPortal';

export default () => {
  const currentUserId = useCurrentUserId();
  const columns = [
    {
      title: '',
      dataIndex: 'classname',
      key: 'classname',
    },
    {
      title: '',
      dataIndex: 'action',
      key: 'action',
      render: (text, record) => {
        return (
          <BtnGroup>
            <Link
              to={{
                pathname: '/giao-vien/sinh-hoat-lop',
                search: `?classId=${record.classId}`,
              }}
            >
              <Btn primary>Sinh hoạt lớp</Btn>
            </Link>
            <Link
              to={{
                pathname: '/giao-vien/cham-diem-ren-luyen',
                search: `?classId=${record.classId}`,
              }}
            >
              <Btn primary>Điểm RL</Btn>
            </Link>
          </BtnGroup>
        );
      },
    },
  ];
  return (
    <div className="comp-OwnerClasses">
      <TableData
        pagination={false}
        columns={columns}
        paramSearch={{ staffId: currentUserId }}
        service={getTeacherOwnerClass}
      />
    </div>
  );
};



