import React from 'react';
import useCurrentUserId from '../../../hook/useCurrentUserId';
import TableData from '../../../react-uikit/component/table/TableData';
import { dateToString } from '../../../react-uikit/utils/formatUtil';
import { getTeacherTestOrganizations } from '../../../services/educationPortal';

export default () => {
  const currentUserId = useCurrentUserId();
  const columns = [
    {
      title: 'Tên môn',
      dataIndex: 'subject',
      key: 'subject',
    },
    {
      title: 'Phòng',
      dataIndex: 'room',
      key: 'room',
    },
    {
      title: 'Số SV',
      dataIndex: 'noOfStudent',
      key: 'noOfStudent',
    },
    {
      title: 'Ngày thi',
      dataIndex: 'testDate',
      key: 'testDate',
      render: (text, record) => {
        return dateToString(record.testDate);
      },
    },
    {
      title: 'Giờ thi',
      dataIndex: 'testTime',
      key: 'testTime',
      render: (text, record) => {
        return `${record.testTime} -> ${record.endTime}`;
      },
    },
  ];
  return (
    <div>
      <TableData
        pagination={false}
        columns={columns}
        paramSearch={{ teacherId: currentUserId }}
        service={getTeacherTestOrganizations}
      />
    </div>
  );
};



